<template>
  <v-container fluid>
    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <v-flex>
         <h1>New Member</h1>     
         <v-text-field
            name="fullname"
            label="Full Name"
            single-line
            v-model="member.fullname"
            prepend-icon="person"
         ></v-text-field>
      <v-menu
        ref="menu"
        lazy
        :close-on-content-click="false"
        v-model="menu"
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        min-width="290px"
        :return-value.sync="member.birthday"
      >
        <v-text-field
          slot="activator"
          label="Birthday"
          v-model="member.birthday"
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker v-model="member.birthday" no-title scrollable>
          <v-spacer></v-spacer>
          <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
          <v-btn flat color="primary" @click="$refs.menu.save(member.birthday)">OK</v-btn>
        </v-date-picker>
      </v-menu>
         <v-text-field
            name="phone"
            label="Phone"
            single-line
            v-model="member.phone"
            prepend-icon="phone"
         ></v-text-field>
         <v-text-field
            name="email"
            label="Email"
            single-line
            v-model="member.email"
            prepend-icon="email"
         ></v-text-field>
         <h1>Preview</h1>
         <p>Name: {{ member.fullname }}</p>
         <p>Birthday: {{ member.birthday}}</p>
         <p>Phone: {{ member.phone }}</p>
         <p>Email: {{ member.email }}</p>
         <v-btn @click="addRecord()">Save</v-btn>
        </v-flex>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>

<script>
import axios from 'axios'
export default {

  data () {
    return {
      member: {
        fullname: '',
        birthday: '',
        phone: '',
        email: ''
      },
      menu: ''
    }
  },
  methods: {
    addRecord () {
      axios({
        method: 'post',
        url: 'https://fir-fdc0f.firebaseio.com/members.json',
        data: {
          fullname: this.member.fullname,
          birthday: this.member.birthday,
          phone: this.member.phone,
          email: this.member.email
        }
      })
      .then(response => {
        this.$swal(this.member.fullname + ' is added to the database', '', 'success')
        this.$router.replace('Members')
      })
    }
  }
}
</script>


