<template>
  <v-container fluid>
    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <v-flex>
         <h1>Member Profile</h1>
        <hr>
        <p>Full Name : {{ member.fullname }}</p>
        <p>Birthday : {{ member.birthday }}</p>
        <p>Phone : {{ member.phone }}</p>
        <p>Emaili : {{ member.email }}</p>
        <v-btn v-bind:to="'/editRecord/' + this.$route.params.id">Edit</v-btn>
        <v-btn v-on:click="deleteRecord()">Delete</v-btn>
        </v-flex>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
hr {
    margin-bottom: 20px;
}
</style>

<script>
import axios from 'axios'
export default {

  data () {
    return {
      id: this.$route.params.id,
      member: {}
    }
  },
  methods: {
    deleteRecord () {
      this.$swal({
        title: 'Are you sure?',
        text: 'This cannot revert your action',
        type: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Yes, delete it!',
        cancelButtonText: 'No, keep it!',
        showCloseButton: true,
        showLoaderOnConfirm: true
      })
      .then(() => {
        axios({
          method: 'delete',
          url: 'https://fir-fdc0f.firebaseio.com/members/' + this.id + '.json'
        })
        .then(() => {
          this.$router.push('/members')
          this.$swal('Deleted', '', 'success')
        })
      })
        .catch(() => {
          this.$swal('Canceled', '', 'info')
        })
    }
  },
  created () {
    axios({
      method: 'get',
      url: 'https://fir-fdc0f.firebaseio.com/members/' + this.id + '.json'
    })
    .then(response => {
      this.member = response.data
    })
  }
}
</script>


