<template>
  <v-container fluid>
    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <v-flex>
         <v-text-field
         label="Search"
         v-model="search"
         append-icon="search"
         ></v-text-field>
         <h1>Members Section
         <v-btn flat icon color="red" @click="logout">
           <v-icon x-large>exit_to_app</v-icon>
         </v-btn>
         </h1>
         <hr>
         <ul>
           <li v-for="member in searchMember">
             <router-link v-bind:to="'/showrecord/' + member.id ">{{ member.fullname }}</router-link> : {{ member.phone }} 
           </li>
         </ul>
        </v-flex>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ul {
  margin-top: 20px;
}
</style>

<script>
import axios from 'axios'
import firebase from 'firebase'

export default {

  data () {
    return {
      members: [],
      search: ''
    }
  },
  methods: {
    logout () {
      firebase.auth().signOut()
      .then(() => {
        this.$router.replace('Login')
      })
    }
  },
  computed: {
    searchMember: function () {
      return this.members.filter(member => {
        return member.fullname.match(this.search) || member.birthday.match(this.search) || member.phone.match(this.search) || member.email.match(this.search)
      })
    }
  },
  created () {
    axios({
      method: 'get',
      url: 'https://fir-fdc0f.firebaseio.com/members.json'
    })
    .then(response => {
      let memberArray = []
      for (let key in response.data) {
        response.data[key].id = key
        memberArray.push(response.data[key])
      }
      this.members = memberArray
    })
  }
}
</script>


