<template>
  <v-container fluid>
    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <v-flex>
         <h1>Login</h1>
         <v-text-field
            name="email"
            single-line
            v-model="email"
            prepend-icon="mail"
         ></v-text-field>
         <v-text-field
            name="password"
            single-line
            type="password"
            v-model="password"
            prepend-icon="vpn_key"
         ></v-text-field>
         <v-btn flat icon color="blue" @click="login()">
           <v-icon x-large>send</v-icon>
         </v-btn>
        </v-flex>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>

<script>
import firebase from 'firebase'

export default {

  data () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    login () {
      firebase.auth().signInWithEmailAndPassword(this.email, this.password)
      .then(user => {
        this.$swal('Success', '', 'success')
        this.$router.replace('Members')
      })
      .catch(() => {
        this.$swal('Error', 'Invalid username/password', 'error')
      })
    }
  }
}
</script>


