<template>
  <v-container fluid>
    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <v-flex>
         <h1>Sign-Up</h1>
         <v-text-field
            name="email"
            label="Email"
            single-line
            v-model="email"
         ></v-text-field>
         <v-text-field
            name="password"
            single-line
            type="password"
            v-model="password"
         ></v-text-field>
         <v-btn @click="signUp()">Signup</v-btn>
        </v-flex>
      </v-layout>
    </v-slide-y-transition>
  </v-container>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>

<script>
import firebase from 'firebase'

export default {

  data () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    signUp () {
      firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
      .then(user => {
        this.$swal('Success', 'Your account has been created', 'success')
        this.email = null
        this.password = null
      })
      .catch(() => {
        this.$swal('Error', 'There is an error', 'error')
      })
    }
  }
}
</script>


